  <view class="tabbar">

    1111
    <view v-for="(item, index) in tabbar_list" :key="index" class="tabbar-item" @click="switchTab(index)">
      <text>{{ item.text }}</text>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      tabbar_list: [{
        icon: '/static/images/tabbar/menu-home-def.png',
        activeIcon: '/static/images/tabbar/menu-home.png',
        text: '列表',
        url: '/pages/home/index',
        name: 'home'
      }, {
        icon: '/static/images/tabbar/menu-mine-def.png',
        activeIcon: '/static/images/tabbar/menu-mine.png',
        text: '我的',
        url: '/pages/mine/index',
        name: 'mine'
      }]
    }
  },
  methods: {
    switchTab(index) {
      // this.$emit('switchTab', index);
      uni.switchTab({
        url: this.tabbar_list[index].url
      })
    }
  }
}
</script>

